/// <reference types="chrome"/>

const htmlRegExp = /((<![^>]+>)|(^<div))/i;

class Utils {
  /**
   * Makes an API request using fetch.
   *
   * @param {Object} params - The request parameters.
   * @param {string} [params.method='GET'] - The HTTP method (GET, POST, PUT, DELETE, etc.).
   * @param {any} [params.data] - The request body data.
   * @param {string} params.url - The request URL.
   * @param {Record<string, string>} [params.headers={}] - The request headers.
   * @param {AbortSignal} [params.signal] - An AbortSignal to cancel the request.
   * @returns {Promise<any>} A promise that resolves with the response data or rejects with an error.
   */
  async apiRequest(params: {
    method?: string;
    data?: any;
    url: string;
    headers?: Record<string, string>;
    signal?: AbortSignal;
  }): Promise<any> {
    const { method = 'GET', data, url, headers = {}, signal } = params;
    const requestOptions: RequestInit = {
      method,
      headers,
      signal,
    };

    let processedData = data;
    let requestUrl = url;

    if (['POST', 'PUT'].includes(method.toUpperCase())) {
      this.#configureBodyRequest(requestOptions, processedData, headers);
    } else if (data && Object.keys(data).length) {
      requestUrl = this.#buildGetRequestUrl(url, processedData);
    }

    requestOptions.headers = headers;

    return fetch(requestUrl, requestOptions).then(this.#handleResponse).catch(this.#handleError);
  }

  /**
   * Configures the request body and headers for POST/PUT requests.
   *
   * @param {RequestInit} requestOptions - The request options object.
   * @param {any} data - The request body data.
   * @param {Record<string, string>} headers - The request headers.
   */
  #configureBodyRequest(requestOptions: RequestInit, data: Object, headers: Record<string, string>) {
    let processedData = data;
    const contentType = Object.keys(headers).find((headerKey) => headerKey.toLowerCase() === 'content-type');

    if (!contentType) {
      headers['Content-Type'] = 'application/json';
      processedData = JSON.stringify(data);
    } else {
      const isFormUrlencoded = headers[contentType].includes('application/x-www-form-urlencoded');

      if (isFormUrlencoded) {
        processedData = this.#convertObjectToFormUrlEncoded(data);
      }
    }

    requestOptions.body = processedData as BodyInit;
  }

  /**
   * Builds the request URL for GET requests with query parameters.
   *
   * @param {string} url - The base URL.
   * @param {Object} data - The query parameters.
   * @returns {string} The complete URL with query parameters.
   */
  #buildGetRequestUrl(url: string, data: Object): string {
    let getParams = '';

    for (const [key, value] of Object.entries(data)) {
      getParams += `${getParams ? `&${key}=${encodeURIComponent(value)}` : `${key}=${encodeURIComponent(value)}`}`;
    }

    return `${url}${getParams ? `?${getParams}` : ''}`;
  }

  /**
   * Handles the fetch response, parsing JSON or text based on the Content-Type.
   *
   * @param {Response} response - The fetch response object.
   * @returns {Promise<any>} A promise that resolves with the parsed response data.
   * @throws {Response} If the response is not ok (status code not in the 200-299 range).
   */
  #handleResponse = async (response: Response): Promise<any> => {
    const cloneResponse = response.clone();

    if (!response.ok) throw response;

    if (response.headers.get('Content-Type')?.includes('application/json')) {
      return response.json();
    } else {
      const cloneText = await cloneResponse.text();
      const isHtml = htmlRegExp.test(cloneText.trim());
      if (!isHtml) {
        return JSON.parse(cloneText);
      } else {
        return response.text();
      }
    }
  };

  /**
   * Handles fetch errors, logging the error and adding response data to the error object if available.
   *
   * @param {any} error - The error object.
   * @returns {Promise<void>} A promise that rejects with the enhanced error object.
   * @throws {any} The enhanced error object.
   */
  #handleError = async (error: any): Promise<void> => {
    console.error('Fetch error:', error);

    if (error instanceof Response) {
      try {
        if (error.headers.get('Content-Type')?.includes('application/json')) {
          const json = await error.json();

          if (!json) throw error;

          (error as any).responseJSON = json;
          (error as any).responseText = JSON.stringify(json);
        } else {
          const cloneError = error.clone();
          const errorText = await cloneError.text();

          if (!errorText) throw error;

          const isHtml = /<\/?[a-z][\s\S]*>/i.test(errorText);

          if (!isHtml) {
            (error as any).responseJSON = JSON.parse(errorText);
            (error as any).responseText = errorText;
          } else {
            (error as any).responseText = errorText;
          }
        }
      } catch (err) {
        console.error('Error while handling the fetch error:', err);
        throw error;
      }
      throw error;
    } else {
      if (/Maximum call stack size exceeded/i.test(error?.message)) {
        chrome.runtime.reload();
      }
      throw error;
    }
  };

  /**
   * Converts a data object to an x-www-form-urlencoded string.
   *
   * @param {Object} data - The data object to convert.
   * @returns {string} An x-www-form-urlencoded string.
   */
  #convertObjectToFormUrlEncoded(data: Object): string {
    const query = new URLSearchParams();

    const buildQuery = (obj: Object, prefix = '') => {
      for (const [key, value] of Object.entries(obj)) {
        const prefixedKey = prefix ? `${prefix}[${key}]` : key;

        if (typeof value === 'object' && !Array.isArray(value)) {
          buildQuery(value, prefixedKey);
        } else if (Array.isArray(value)) {
          value.forEach((val) => query.append(`${prefixedKey}[]`, val));
        } else {
          if (typeof value === 'string') {
            query.append(prefixedKey, value);
          }
        }
      }
    };

    buildQuery(data);
    return query.toString();
  }
}

export default new Utils();
