import { IKeyParamsSite, KeyOffersMetaDataResponse, KeyOffersResponse } from '../../../../global/models/keys.model';
import KeysService from './keys.service';

export default {
  msg: {
    getKeyOffers: {
      code: 'TS_GET_KEY_OFFERS',
      handler: async (data: IKeyParamsSite, cb: (response: KeyOffersResponse) => void) => {
        try {
          const response = await KeysService.getKeyOffers(data);
          cb(response);
        } catch (e: any) {
          cb({ success: false, error: e.message || e.responseJSON || 'Error, something went wrong' });
        }
      },
    },

    getMeta: {
      code: 'TS_GET_KEY_OFFERS_METADATA',
      handler: async (cb: (response: KeyOffersMetaDataResponse) => void) => {
        try {
          const response = await KeysService.getMeta();
          cb(response);
        } catch (e: any) {
          cb({ success: false, error: e.message || e.responseJSON || 'Error, something went wrong' });
        }
      },
    },
  },
};
