import {
  IKeyOffersMetaApiResponse,
  IKeyParamsSite,
  KeyOffersMetaDataResponse,
  KeyOffersResponse,
} from '../../../../global/models/keys.model';
import Utils from '../../lib/Utils';
import KeysMapper from './utils/keys.mapper';

class KeysService {
  #apiUrlV2 = 'https://gamestats.steaminventoryhelper.com/api/v2/';

  async getKeyOffers(params: IKeyParamsSite): Promise<KeyOffersResponse> {
    try {
      const mappedParams = KeysMapper.getKeyOffersSiteToApiMapper(params);

      const queryParams = new URLSearchParams();

      //TODO вынести это в какой-то utils
      Object.entries(mappedParams).forEach(([key, value]) => {
        if (value === undefined || value === null) return;

        if (Array.isArray(value)) {
          if (value.length === 0) return;
          queryParams.set(key, value.join(','));
        } else {
          queryParams.set(key, String(value));
        }
      });

      const response = await Utils.apiRequest({
        url: `${this.#apiUrlV2}key-offers?m=1&${queryParams.toString()}`,
      });

      return KeysMapper.getKeyOffersApiToSiteMapper(response);
    } catch (e: any) {
      return { success: false, error: e.message || e.responseJSON || 'Error, something went wrong' };
    }
  }

  async getMeta(): Promise<KeyOffersMetaDataResponse> {
    try {
      const response: IKeyOffersMetaApiResponse = await Utils.apiRequest({
        url: `${this.#apiUrlV2}key-offers/meta?m=1`,
      });

      if (response.e && response.f) {
        const mappedData = KeysMapper.getKeyOffersMetaApiToSiteMapper(response.f);

        return {
          success: response.e,
          data: mappedData,
        };
      } else {
        throw new Error(`Unable to get meta data`);
      }
    } catch (e: any) {
      return { success: false, error: e.message || e.responseJSON || 'Error, something went wrong' };
    }
  }
}

export default new KeysService();
