/// <reference types="chrome"/>
class Storage {
  get(params: Record<string, any> | string): Promise<any> {
    return new Promise((resolve, reject) => {
      chrome.storage.sync.get(params, (result) => {
        if (chrome.runtime.lastError) {
          reject(chrome.runtime.lastError);
        } else {
          resolve(result);
        }
      });
    });
  }

  set(params: Record<string, any>): Promise<void> {
    return new Promise((resolve, reject) => {
      chrome.storage.sync.set(params, () => {
        if (chrome.runtime.lastError) {
          reject(chrome.runtime.lastError);
        } else {
          resolve();
        }
      });
    });
  }

  setByName({ name, value }: { name: string; value: any }): Promise<void> {
    return this.set({ [name]: value });
  }

  getLocal(params: Record<string, any> | string): Promise<any> {
    return new Promise((resolve, reject) => {
      chrome.storage.local.get(params, (result) => {
        if (chrome.runtime.lastError) {
          reject(chrome.runtime.lastError);
        } else {
          resolve(result);
        }
      });
    });
  }

  setLocal(params: Record<string, any>): Promise<void> {
    return new Promise((resolve, reject) => {
      chrome.storage.local.set(params, () => {
        if (chrome.runtime.lastError) {
          reject(chrome.runtime.lastError);
        } else {
          resolve();
        }
      });
    });
  }

  setLocalByName({ name, value }: { name: string; value: any }): Promise<void> {
    return this.setLocal({ [name]: value });
  }

  clear(storage = 'sync'): void {
    if (storage === 'sync') {
      chrome.storage.sync.clear(() => {});
    } else {
      chrome.storage.local.clear(() => {});
    }
  }
}

export default new Storage();
