import { SIH_API_URL } from '../../lib/Constants';
import Storage from '../../lib/Storage';
import Utils from '../../lib/Utils';
import { ConvertCurrencyRates, CurrencyRatesResponse } from '../../../../global/models/rates.model';

class RatesService {
  //TODO: REMOVE THIS METHOD AFTER TESTS
  async getTestRates(): Promise<Object> {
    try {
      return await Utils.apiRequest({
        url: 'https://api.steaminventoryhelper.com/rates?base=USD',
      });
    } catch (e: any) {
      return { success: false, error: e.message || e.responseJSON || 'Error, something went wrong' };
    }
  }

  async setGlobalRates(): Promise<void> {
    try {
      const { currency_code } = await Storage.get('currency_code');
      const { userInfo } = await Storage.getLocal('userInfo');
      const { steamCurrency = { strCode: 'USD' } } =
        !userInfo?.authorization || (await Storage.get({ steamCurrency: { strCode: 'USD' } }));

      const globalRatesByUserCurrency = await this.#fetchRates(currency_code || steamCurrency.strCode);
      const defaultGlobalRates = await this.#fetchRates('USD');

      if (globalRatesByUserCurrency.success) {
        await Storage.set({ currency_rates: globalRatesByUserCurrency.data });
        await Storage.set({ default_currency_rates: defaultGlobalRates.data });

        const convertCurrencyRates = await this.#getConvertRates(globalRatesByUserCurrency);
        await Storage.set({ global_currency_rates: convertCurrencyRates });
      }
    } catch (error) {
      console.error(error);
    }
  }

  async setSteamRates(): Promise<void> {
    try {
      const { currency_code } = await Storage.get('currency_code');
      const { userInfo } = await Storage.getLocal('userInfo');
      const { steamCurrency = { strCode: 'USD' } } =
        !userInfo?.authorization || (await Storage.get({ steamCurrency: { strCode: 'USD' } }));

      const steamRatesByUserCurrency = await this.#fetchRates(currency_code || steamCurrency.strCode, 'steam-rates');
      const defaultSteamRates = await this.#fetchRates('USD', 'steam-rates');

      if (steamRatesByUserCurrency.success) {
        await Storage.set({ steam_rates: steamRatesByUserCurrency.data });
        await Storage.set({ default_steam_rates: defaultSteamRates.data });

        const convertCurrencyRates = await this.#getConvertRates(steamRatesByUserCurrency);
        await Storage.set({ steam_currency_rates: convertCurrencyRates });
      }
    } catch (error) {
      console.error(error);
    }
  }

  #getConvertRates = (rates: CurrencyRatesResponse): Promise<ConvertCurrencyRates> => {
    return new Promise((resolve) => {
      const amount: number[] = [1, 100, 1000, 10000];
      const { base, rates: ratesInfo } = rates.data;
      const convertCurrencyRates: ConvertCurrencyRates = { base, rates: {}, ts: rates.data.ts };

      Object.entries(ratesInfo).forEach(([strCode, rate]) => {
        if (strCode === base) {
          convertCurrencyRates.rates[strCode] = { count: 1, number: rate };
          return;
        }

        const baseRate = ratesInfo[base];
        let number = baseRate / rate;

        // Count leading zeros after decimal point
        const matchResult = number.toString().match(/^0\.0*/);
        const leadingZeros = matchResult ? matchResult[0].length - 2 : 0;

        // Apply multiplier based on number of leading zeros
        if (leadingZeros > 0) {
          const multiplier = amount[Math.min(leadingZeros, 3)];
          number *= multiplier;
        }

        convertCurrencyRates.rates[strCode] = {
          count: Math.min(leadingZeros, 3) > 0 ? amount[Math.min(leadingZeros, 3)] : 1,
          number,
        };
      });

      resolve(convertCurrencyRates);
    });
  };

  #fetchRates(base: string, endPoint: string = 'rates'): Promise<CurrencyRatesResponse> {
    return Utils.apiRequest({
      url: `${SIH_API_URL}/${endPoint}`,
      data: {
        base,
      },
    });
  }
}

export default new RatesService();
