import RatesService from './rates.service';

export default {
  msg: {
    //TODO: REMOVE THIS
    getTestRates: {
      code: 'TEST_MESSAGE',
      handler: async (cb: (response: any) => void) => {
        try {
          const response = await RatesService.getTestRates();
          cb(response);
          //TODO: Change any
        } catch (e: Object | any) {
          cb(e);
        }
      },
    },
  },
  alarm: {
    getGlobalRates: {
      code: 'BACKGROUND_ALARM_SET_EXCHANGE_RATES',
      handler: async () => {
        try {
          await RatesService.setGlobalRates();
        } catch (e) {
          console.log(e);
        }
      },
    },
    getSteamRates: {
      code: 'BACKGROUND_ALARM_SET_STEAM_RATES',
      handler: async () => {
        try {
          await RatesService.setSteamRates();
        } catch (e) {
          console.log(e);
        }
      },
    },
  },
};
