import Rates from './rates.handler';

const handleAlarms = () => {
  Rates.alarmListener.init.handler();
};
const handleMessages = () => {
  Rates.msgListener.init.handler();
};
export default {
  initListener: {
    handler: () => {
      try {
        handleAlarms();
        handleMessages();
      } catch (e) {
        console.log(e);
      }
    },
  },
};
